var users={
    "a":{
        "password":"a"
        ,"qns":["qid1","qid2"]
    }
}
var qns={
    "qid1":{
        "user":"a"
        ,"title":"title1"
        ,"start":"time1"
        ,"end":"time2"
        ,"state":"state1"
        ,"qs":[
            {
                necessary:true
                ,type:'radio'
                ,describe:'describe1'
                ,choices:['c1','c2','c3','c4']
            }
            ,{
                necessary:false
                ,type:'checkbox'
                ,describe:'describe2'
                ,choices:['c1','c2','c3','c4']
            }
            ,{
                necessary:false
                ,type:'text'
                ,describe:'describe3'
                ,placeholder:'placeholder'
            }
        ]
    }
    ,"qid2":{
        "user":"a"
        ,"title":"title2"
        ,"start":"time3"
        ,"end":"time4"
        ,"state":"state2"
        ,"qs":[
            {
                necessary:true
                ,type:'radio'
                ,describe:'describe1'
                ,choices:['c1','c2','c3','c4']
            }
            ,{
                necessary:false
                ,type:'checkbox'
                ,describe:'describe2'
                ,choices:['c1','c2','c3','c4']
            }
            ,{
                necessary:false
                ,type:'text'
                ,describe:'describe3'
                ,placeholder:'placeholder'
            }
        ]
    }
}
var results={
    "qid1":[]
    ,"qid2":[]
}
users=JSON.stringify(users)
qns=JSON.stringify(qns)
results=JSON.stringify(results)
window.localStorage.users=users
window.localStorage.qns=qns
window.localStorage.results=results



